import React from 'react'
import { Formik, Form, Field } from 'formik'
import { Button, Form as FormAnt, Alert } from 'antd'
import * as Yup from 'yup'
import { EyeInvisibleOutlined, EyeTwoTone } from '@ant-design/icons'
import { validations } from '../../../services/yupValidations'
import { useHistory } from 'react-router'

import { LoginInPath } from '../../../constants/routerConstants'
import { countrys } from './../../../constants/countrys'
import { availableLangues } from './../../../constants/language'

import { useTranslation } from 'react-i18next'
import {
    AntInput,
    AntInputPassword,
    AntSelect,
} from '../../../components/CreateAntField/index'

const schema = Yup.object({
    firstName: validations.name,
    lastName: validations.name,
    email: validations.mail,
    country: validations.generic,
    language: validations.generic,
    password: validations.password,
    confirmPassword: validations.passwordConfirmation,
})

const initialValues = {
    firstName: '',
    lastName: '',
    userName: '',
    email: '',
    // country: null,
    password: '',
    confirmPassword: '',
}

const SigUp = ({
    onSubmit,
    showError,
    errorMessage,
    disabled,
    walletAddress,
}) => {
    const { t } = useTranslation(['auth', 'commons'])
    const history = useHistory()

    return (
        <React.Fragment>
            <div className="font-medium mb-2">{t('commons:signUp')}</div>
            <Formik
                validationSchema={schema}
                initialValues={initialValues}
                onSubmit={onSubmit}
            >
                {({ values, isSubmitting }) => (
                    <Form className="mb-4">
                        <div className="flex flex-row space-x-2">
                            <Field
                                component={AntInput}
                                type="text"
                                name="firstName"
                                placeholder={`* ${t(
                                    'commons:inputs.first_name'
                                )}`}
                                className="h-12 rounded-lg"
                                hasFeedback
                                value={values?.firstName}
                            />
                            <Field
                                component={AntInput}
                                type="text"
                                name="lastName"
                                placeholder={`* ${t(
                                    'commons:inputs.last_name'
                                )}`}
                                className="h-12 rounded-lg"
                                hasFeedback
                                value={values?.lastName}
                            />
                        </div>
                        <Field
                            component={AntInput}
                            name="email"
                            type="email"
                            placeholder={t('commons:inputs.email')}
                            className="h-12 rounded-lg"
                            hasFeedback
                            value={values?.email.toLowerCase()}
                        />
                        <Field
                            component={AntSelect}
                            name="country"
                            showSearch
                            // type="email"
                            placeholder={t('commons:inputs.country')}
                            className="h-12 rounded-lg custom-ant-select"
                            selectOptions={countrys}
                            // hasFeedback
                            value={values?.country}
                        />
                        <Field
                            component={AntSelect}
                            name="language"
                            showSearch
                            // type="email"
                            placeholder={t('commons:inputs.language')}
                            className="h-12 rounded-lg custom-ant-select"
                            selectOptions={availableLangues}
                            // hasFeedback
                            value={values?.language}
                        />
                        {/* <Field
                            component={AntInput}
                            type="text"
                            name="userName"
                            placeholder="Username"
                            className="h-12 rounded-lg"
                            hasFeedback
                            value={values?.userName}
                        /> */}
                        <Field
                            component={AntInputPassword}
                            name="password"
                            type="password"
                            placeholder={t('commons:inputs.password')}
                            className="h-12 rounded-lg"
                            hasFeedback
                            iconRender={(visible) =>
                                visible ? (
                                    <EyeTwoTone />
                                ) : (
                                    <EyeInvisibleOutlined />
                                )
                            }
                            value={values?.password}
                        />
                        <Field
                            component={AntInputPassword}
                            name="confirmPassword"
                            type="password"
                            placeholder={t('commons:inputs.confirm_password')}
                            className="h-12 rounded-lg"
                            hasFeedback
                            iconRender={(visible) =>
                                visible ? (
                                    <EyeTwoTone />
                                ) : (
                                    <EyeInvisibleOutlined />
                                )
                            }
                            value={values?.confirmPassword}
                        />
                        {showError && (
                            <Alert
                                className="mb-5"
                                message={errorMessage}
                                type="error"
                                showIcon
                            />
                        )}

                        <FormAnt.Item className="mb-0">
                            <Button
                                type="primary"
                                block
                                className="text-sm px-4 py-2 py h-12 rounded-full font-medium"
                                htmlType="submit"
                                loading={isSubmitting}
                                style={{
                                    paddingTop: '9px',
                                    paddingBottom: '9px',
                                }}
                            >
                                {t('commons:signUp')}
                            </Button>
                        </FormAnt.Item>
                    </Form>
                )}
            </Formik>

            <div className="login-links text-center">
                <span
                    className="text-black-1 text-sm h-auto py-0 relative"
                    style={{ top: '-7px' }}
                >
                    {t('auth:signup.already_account')}
                    <a
                        className="font-medium text-blue-200 hover:text-info"
                        href="#"
                        onClick={(e) => {
                            e.preventDefault()

                            history.push(LoginInPath)
                        }}
                    >
                        {' '}
                        {t('commons:login')}
                    </a>
                </span>
            </div>
            {!showError && <div className="h-40px mb-5"></div>}
        </React.Fragment>
    )
}

SigUp.propTypes = {}

export default SigUp
